import styled from "styled-components";
import { IToken } from "../../../utils/interface";
import TokenButton from "../../TokenButton";
import { useToken } from "../../../hooks/convert";

const Wrapper = styled.div`
  display: grid;
  grid-auto-rows: auto;
  row-gap: 12px;

  .AutoRows {
    flex-wrap: wrap;
    margin: -4px;
    width: 100%;
    display: flex;
    padding: 0px;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: start;
    justify-content: flex-start;
    gap: 14px;
    box-sizing: border-box;
    min-width: 0px;
  }
`;

const Suggestions = ({
  tokens,
  onSelect,
  selectedToken,
}: {
  tokens: IToken[];
  onSelect: (token: IToken) => void;
  selectedToken: IToken | undefined;
}) => {
  const { topToken, bottomToken } = useToken();
  return (
    <Wrapper>
      <div className="AutoRows">
        {tokens.map((item, index) => (
          <TokenButton
            token={item}
            fontSize={16}
            isDisabled={[topToken, bottomToken].includes(item)}
            onClick={() => onSelect(item)}
          />
        ))}
      </div>
    </Wrapper>
  );
};

export default Suggestions;
