import styled, { keyframes } from "styled-components";
import { useRelationTokenAmount, useToken } from "../../hooks/convert";

const spin = keyframes`
0% {
  transform: rotate(0deg);
}
100% {
  transform: rotate(360deg);
}
`;

const Wrapper = styled.div`
  border: 1px solid rgba(255, 255, 255, 0.07);
  border-radius: 16px;
  padding: 12px 16px;
  display: flex;
  flex-direction: column;
  -webkit-box-pack: start;
  justify-content: flex-start;
  margin-top: 4px;
`;

const HeaderRow = styled.div`
  padding: 0px;
  -webkit-box-align: center;
  align-items: center;
  cursor: pointer;
  -webkit-box-pack: justify;
  justify-content: space-between;
  width: 100%;
  display: flex;
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
`;

const FixedRow = styled.div`
  width: fit-content;
  display: flex;
  padding: 0px;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: start;
  justify-content: flex-start;
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
`;

const LoadingOpacityContainer = styled.div`
  display: flex;
  height: 16px;
  width: 16px;
  margin-right: 2px;
  margin-left: 2px;
  -webkit-box-align: center;
  align-items: center;
  color: rgb(255, 255, 255);
  transition: color 250ms ease 0s;

  .Loading {
    width: 8px;
    height: 8px;
    min-height: 8px;
    min-width: 8px;
    border-radius: 50%;
    position: relative;
    background-color: rgba(255, 255, 255, 0.07);
    transition: background-color 250ms ease 0s;

    .SpinAnimation {
      animation: 1s cubic-bezier(0.83, 0, 0.17, 1) 0s infinite normal none
        running ${spin};
      transform: translateZ(0px);
      border-width: 1px 1px 1px 2px;
      border-style: solid;
      border-color: transparent transparent transparent rgb(255, 255, 255);
      background: transparent;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      position: relative;
      transition: border-color 250ms ease 0s;
      left: -3px;
      top: -3px;
    }
  }
`;

const TextWrapper = styled.div`
  color: ${(props) => props.color || "inherit"};
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
  font-weight: 485;
  font-size: 14px;
`;

const SwapDetail = () => {
  const { isStartLoading, topToken, bottomToken } = useToken();
  const relationTokenAmount = useRelationTokenAmount();
  return (
    <Wrapper>
      <HeaderRow data-testid="swap-details-header-row">
        <FixedRow>
          {isStartLoading && (
            <LoadingOpacityContainer>
              <div className="Loading">
                <div className="SpinAnimation"></div>
              </div>
            </LoadingOpacityContainer>
          )}
          {relationTokenAmount && (
            <TextWrapper color="white">{`1 ${bottomToken.key} = ${relationTokenAmount} ${topToken.key}`}</TextWrapper>
          )}
        </FixedRow>
      </HeaderRow>
    </Wrapper>
  );
};

export default SwapDetail;
