import styled from "styled-components";
import { useMemo, useState } from "react";
import { IToken } from "../../utils/interface";
import TokenList from "./TokenList";
import ModalHeader from "./Header";
import SearchInput from "./SearchInput";
import Suggestions from "./SuggestedTokens";
import { tokenLists } from "../../utils/data";
import { useToken } from "../../hooks/convert";

type IProps = {
  currentAmount?: number;
  onClose: () => void;
  selectedToken: IToken;
  onSelectToken: () => void;
};

const ModalConatainer = styled.div`
  margin: auto;
  background-color: rgb(27, 27, 27);
  border: 1px solid rgba(255, 255, 255, 0.07);
  box-shadow: rgba(0, 0, 0, 0.24) 12px 16px 24px,
    rgba(0, 0, 0, 0.24) 12px 8px 12px, rgba(0, 0, 0, 0.32) 4px 4px 8px;
  padding: 0px;
  width: 50vw;
  overflow: hidden auto;
  max-width: 420px;
  max-height: 80vh;
  min-height: 80vh;
  display: flex;
  border-radius: 20px;
  outline: none;

  .ContentWrapper {
    background-color: rgb(19, 19, 19);
    width: 100%;
    overflow: hidden;
    flex: 1 1 0%;
    position: relative;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    -webkit-box-pack: start;
    justify-content: flex-start;

    .split {
      width: 100%;
      height: 1px;
      background-color: rgba(255, 255, 255, 0.07);
    }

    .SearchContainer {
      padding: 20px;
      display: grid;
      grid-auto-rows: auto;
      row-gap: 16px;
    }
  }
`;

const ListTokenContainer = styled.div`
  flex: 1 1 0%;
  position: relative;
`;

const SectionTokenModal = ({
  currentAmount,
  onClose,
  selectedToken,
  onSelectToken,
}: IProps) => {
  const [searchInput, setSearchInput] = useState("");
  const data = useMemo(() => {
    const keyword = searchInput.toLowerCase();
    return tokenLists.filter((token) =>
      token.name.toLowerCase().includes(keyword)
    );
  }, [searchInput]);

  const { onChangeAmount, onChooseToken } = useToken();

  const onSelect = (token: IToken) => {
    onSelectToken();
    onChooseToken(selectedToken, token);
    onChangeAmount(token, currentAmount);
  };
  return (
    <ModalConatainer>
      <div className="ContentWrapper">
        <div className="SearchContainer">
          <ModalHeader title="Select a token" onClose={onClose} />
          <SearchInput
            onSearch={(value) => setSearchInput(value)}
            value={searchInput}
          />
          <Suggestions
            tokens={tokenLists.filter((token) => token.isSuggested)}
            onSelect={onSelect}
            selectedToken={selectedToken}
          />
        </div>
        <div className="split" />
        <ListTokenContainer>
          <TokenList
            data={data}
            onSelect={onSelect}
            selectedToken={selectedToken}
          ></TokenList>
        </ListTokenContainer>
      </div>
    </ModalConatainer>
  );
};

export default SectionTokenModal;
