import { createContext, FC, useCallback, useState } from "react";
import { IToken } from "../utils/interface";
import debounce from "lodash.debounce";
import { findPriceOfTokens, roundPrice } from "../utils/price";
import { tokenLists } from "../utils/data";

interface IStore {
  topToken: IToken;
  bottomToken: IToken;
  currentToken?: IToken;
  isStartLoading: boolean;
  convertedTokenAmount?: number;
  topPrice?: number;
  bottomPrice?: number;
  tokenChanged?: { oldToken: IToken; newToken: IToken };
  onChangeAmount: (token: IToken, amount?: number) => void;
  swap: () => void;
  onChooseToken: (oldToken: IToken, newToken: IToken) => void;
}

export const ConverterContext = createContext<IStore>({
  topToken: tokenLists[0],
  bottomToken: tokenLists[1],
  isStartLoading: false,
  onChangeAmount: (token: IToken, amount?: number) => {},
  swap: () => {},
  onChooseToken: (oldToken: IToken, newToken: IToken) => {},
});

export const ConvertProvider: FC<{ children: any }> = ({ children }) => {
  const updateAmount = (amount: number, token: IToken) => {
    setData((prevData) => {
      const isTopToken = token === prevData.topToken;
      const [price, targetPrice] = findPriceOfTokens(
        token,
        isTopToken ? prevData.bottomToken : prevData.topToken
      );
      return {
        ...prevData,
        isStartLoading: false,
        currentToken: token,
        convertedTokenAmount: roundPrice((amount * price) / targetPrice),
        topPrice: isTopToken ? price : targetPrice,
        bottomPrice: isTopToken ? targetPrice : price,
      };
    });
  };
  const debounceUpdateAmount = debounce(updateAmount, 3000);
  const onChangeAmount = (token: IToken, amount?: number) => {
    if (amount) {
      setData((prevData) => ({
        ...prevData,
        isStartLoading: true,
      }));
      debounceUpdateAmount(amount, token);
    } else {
      debounceUpdateAmount.cancel();
      setData((prevData) => ({
        ...prevData,
        currentToken: token,
        isStartLoading: false,
        convertedTokenAmount: undefined,
        topPrice: undefined,
        bottomPrice: undefined,
      }));
    }
  };
  const swap = () =>
    setData((prevData) => ({
      ...prevData,
      topToken: prevData.bottomToken,
      bottomToken: prevData.topToken,
      topPrice: prevData.bottomPrice,
      bottomPrice: prevData.topPrice,
    }));
  const onChooseToken = (oldToken: IToken, newToken: IToken) =>
    setData((prevData) => {
      const isTopToken = oldToken === prevData.topToken;
      return {
        ...prevData,
        ...(isTopToken ? { topToken: newToken } : { bottomToken: newToken }),
        currentToken: undefined,
        tokenChanged: {
          oldToken,
          newToken,
        },
      };
    });

  const [data, setData] = useState<IStore>({
    topToken: tokenLists[0],
    bottomToken: tokenLists[1],
    isStartLoading: false,
    onChangeAmount,
    swap,
    onChooseToken,
  });

  return (
    <ConverterContext.Provider value={data}>
      {children}
    </ConverterContext.Provider>
  );
};
