import styled from "styled-components";

const InputContainer = styled.div`
  width: 100%;
  display: flex;
  padding: 0px;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: start;
  justify-content: flex-start;
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
  height: 40px;

  input {
    background: url("https://app.uniswap.org/static/media/search.3d4b96e3a9ccded7bfd728a818c83611.svg")
      12px center / 20px 20px no-repeat scroll rgb(27, 27, 27);
    position: relative;
    display: flex;
    padding: 16px 16px 16px 40px;
    -webkit-box-align: center;
    align-items: center;
    width: 100%;
    white-space: nowrap;
    outline: none;
    border-radius: 12px;
    color: rgb(255, 255, 255);
    border: 1px solid rgba(255, 255, 255, 0.07);
    appearance: none;
    font-weight: 485;
    font-size: 16px;
    transition: border 100ms ease 0s;
    height: 6px;
  }
`;

const SearchInput = (props: { value: string; onChange: any }) => {
  const { value, onChange } = props;
  return (
    <input
      key="search-token-input"
      type="text"
      autoComplete="off"
      autoCorrect="off"
      placeholder="Search name or paste address"
      value={value}
      onChange={onChange}
    />
  );
};

const SearchInputComponent = (props: {
  value: string;
  onSearch: (evt: any) => void;
}) => {
  const { value, onSearch } = props;
  return (
    <InputContainer>
      <SearchInput
        value={value}
        onChange={(e: any) => onSearch(e.target.value)}
      />
    </InputContainer>
  );
};

export default SearchInputComponent;
