import { useEffect, useState } from "react";
import styled from "styled-components";
import TokenButton from "../TokenButton";
import { IToken } from "../../utils/interface";
import { useToken } from "../../hooks/convert";

type IProps = {
  onOpenModal: (token: IToken, amount?: number) => void;
  initToken: IToken;
};

const SwapSection = styled.div`
  background-color: rgb(27, 27, 27);
  border-radius: 16px;
  color: rgb(155, 155, 155);
  font-size: 14px;
  font-weight: 500;
  height: 84px;
  line-height: 20px;
  padding: 16px;
  position: relative;

  &::before {
    box-sizing: border-box;
    background-size: 100%;
    border-radius: inherit;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    pointer-events: none;
    content: "";
    border: 1px solid rgb(27, 27, 27);
  }

  &:hover::before {
    border-color: rgba(152, 161, 192, 0.08);
  }

  &:focus-within::before {
    border-color: rgba(184, 192, 220, 0.24);
  }
`;

const SwapCurrencyInput = styled.div`
  display: flex;
  flex-flow: column;
  position: relative;
  border-radius: 20px;
  z-index: 1;
  width: initial;
  transition: height 1s ease 0s;
  will-change: height;
`;

const InputPanelContainer = styled.div`
  min-height: 44px;
  border-radius: 20px;
  width: initial;
`;

const TextWrapper = styled.div`
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
  font-weight: 485;
  font-size: 14px;
  user-select: none;
`;

const InputRow = styled.div`
  display: flex;
  flex-flow: row;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  justify-content: space-between;
`;

const AmoutContainer = styled.div`
  display: flex;
  flex-grow: 1;
`;

const TokenContainer = styled.div`
  display: inline-block;
  height: inherit;
`;

const InputField = styled.input.attrs((props: any) => ({
  style: { color: props.color ? props.color : "white" },
}))`
  filter: none;
  opacity: 1;
  transition: opacity 250ms ease-in-out 0s;
  text-align: left;
  font-size: 36px;
  font-weight: 485;
  max-height: 44px;
  pointer-events: auto;
  width: 0px;
  position: relative;
  outline: none;
  border: none;
  flex: 1 1 auto;
  background-color: transparent;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0px;
  appearance: textfield;
`;

const InputAmount = (props: any) => {
  return (
    <InputField
      key="input-amount"
      inputMode="decimal"
      autoComplete="off"
      autoCorrect="off"
      type="text"
      pattern="^[0-9]*[.,]?[0-9]*$"
      placeholder="0"
      minLength={1}
      maxLength={79}
      spellCheck="false"
      onKeyPress={(e: any) => {
        const keyCode = e.which || e.keyCode;
        if (
          (keyCode < 48 || keyCode > 57) &&
          keyCode !== 8 &&
          (keyCode !== 46 || props.value.includes("."))
        ) {
          e.preventDefault();
        }
      }}
      onChange={props.onChange}
      value={props.value}
      color={props.value && props.value !== 0 ? "white" : "rgb(155, 155, 155)"}
    />
  );
};

const SwapSectionComponent = ({ onOpenModal, initToken }: IProps) => {
  const [amount, setAmount] = useState<string>("");
  const [token, setToken] = useState<IToken>(initToken);
  const {
    onChangeAmount,
    topToken,
    currentToken,
    convertedTokenAmount,
    tokenChanged,
  } = useToken();

  useEffect(() => {
    if (currentToken && token !== currentToken) {
      setAmount(convertedTokenAmount?.toString() || "");
    }
  }, [currentToken, convertedTokenAmount]);

  useEffect(() => {
    if (tokenChanged && tokenChanged.oldToken === token) {
      setToken(tokenChanged.newToken);
    }
  }, [tokenChanged]);

  return (
    <SwapSection>
      <SwapCurrencyInput>
        <InputPanelContainer>
          <TextWrapper>
            {token === topToken ? "You pay" : "You receive"}
          </TextWrapper>
          <InputRow>
            <AmoutContainer>
              <InputAmount
                value={amount}
                onChange={(e: any) => {
                  const value = e.target.value;
                  setAmount(value);
                  onChangeAmount(
                    token,
                    value.length > 0 ? parseFloat(value) : undefined
                  );
                }}
              />
            </AmoutContainer>
            <div>
              <TokenContainer>
                <TokenButton
                  isSelectButton
                  token={token}
                  onClick={() =>
                    onOpenModal(
                      token,
                      amount.length > 0 ? parseFloat(amount) : undefined
                    )
                  }
                />
              </TokenContainer>
            </div>
          </InputRow>
        </InputPanelContainer>
      </SwapCurrencyInput>
    </SwapSection>
  );
};

export default SwapSectionComponent;
