import { useContext, useMemo } from "react";
import { ConverterContext } from "../providers/convert";
import { roundPrice } from "../utils/price";

export function useToken() {
  return useContext(ConverterContext);
}

export function useRelationTokenAmount() {
  const { topPrice, bottomPrice } = useContext(ConverterContext);

  const output = useMemo(
    () =>
      topPrice && bottomPrice ? roundPrice(bottomPrice / topPrice) : undefined,
    [topPrice, bottomPrice]
  );
  return output;
}
